* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
  text-align: center;
  box-sizing: border-box;
  background-color: #0a0909;
  margin-top: 100px;
  font-family: prompt;
  font-size: 35px;
  line-height: 70px;
  overflow: hidden;
}

header {
  -webkit-user-select: none;
  user-select: none;
  cursor: default;
}

h1 {
  color: #f07178;
}

.price-tracker {
  background: linear-gradient(to left, #30cfd0 0%, #613a91 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

.price-tracker:hover {
  opacity: .8;
  transition: all .3s;
}

p {
  color: gray;
  align-items: center;
  margin: 0;
  padding: 15px;
  font-size: 15px;
  display: flex;
}

.price {
  color: #cdc7ca;
  margin-left: 10px;
  font-weight: 800;
}

img {
  width: 1rem;
  height: 1rem;
}

.percentage {
  color: #ee2b6f;
  margin-left: 8px;
}

.icons {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.spot {
  margin-left: 3px;
}

.coins-tracker {
  width: 100%;
  height: 60px;
  background-color: #121818;
  margin-top: 100px;
  font-family: Courier New, Courier, monospace;
  display: flex;
  overflow: hidden;
}

.coin {
  height: 100%;
  flex-shrink: 0;
  display: flex;
}

.coin:first-child {
  margin-right: auto;
}

.coin:last-child {
  margin-left: auto;
}

.coins {
  flex: 1;
  list-style: none;
  display: flex;
}

.coins-tracker .coin {
  animation: 15s linear infinite coin-slide;
}

@keyframes coin-slide {
  0% {
    transform: translateX(-100px);
  }

  10% {
    transform: translateX(-200px);
  }

  25% {
    transform: translateX(-300px);
  }

  50% {
    transform: translateX(-500px);
  }

  75% {
    transform: translateX(-700px);
  }

  100% {
    transform: translateX(-1000px);
  }
}

